<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="spotId">
            <!-- 选择景区名称 start -->
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <!-- 选择景区名称 end -->
          </el-form-item>

          <el-form-item label="" prop="name">
            <!-- 输入联系人姓名 start -->
            <!-- <el-input class="input-name" v-model="searchValidateForm.name" placeholder="联系人姓名"></el-input> -->
            <el-select
              v-model="searchValidateForm.name"
              filterable
              placeholder="请选择联系人姓名"
              clearable
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in searchNameOptions"
                :key="index"
                :label="item.userName"
                :value="item.userId"
              >
              </el-option>
            </el-select>
            <!-- 输入联系人姓名 end -->
          </el-form-item>

          <el-form-item label="" prop="phone">
            <!-- 输入联系人电话 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.phone"
              placeholder="联系人电话"
            ></el-input>
            <!-- 输入联系人电话 end -->
          </el-form-item>

          <el-form-item label="" prop="type">
            <!-- 选择地址类型 start -->
            <el-select
              v-model="searchValidateForm.type"
              filterable
              placeholder="请选择地址类型"
              clearable
            >
              <el-option
                v-for="(item, index) in searchTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <!-- 选择地址类型 end -->
          </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->

        <!-- 导入 Excel start -->
        <el-upload
          class="upload-demo"
          action="/system/address/upload"
          :on-success="handleSuccess"
          :on-exceed="handleExceed"
          :on-remove="handleRemove"
          :file-list="fileListUpload"
          :show-file-list="false"
          :limit="limitUpload"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
          v-if="updateUpload"
        >
          <el-button type="primary" icon="el-icon-upload">导入Excel</el-button>
          <!-- <div slot="tip" class="el-upload__tip">只 能 上 传 xlsx / xls 文 件</div> -->
        </el-upload>
        <!-- 导入 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
          v-loading="loadingTable"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <el-form-item label="地址类型" prop="type">
            <!-- 选择地址类型 start -->
            <el-select
              v-model="validateForm.type"
              filterable
              placeholder="请选择地址类型"
              clearable
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in typeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <!-- 选择地址类型 end -->
          </el-form-item>

          <!-- 景区名称 start -->
          <el-form-item label="景区名称" prop="spotId">
            <!-- <el-input type="text" v-model="validateForm.spotName" autocomplete="off" placeholder="请输入景区名称"></el-input> -->
            <el-select
              v-model="validateForm.spotId"
              filterable
              placeholder="请选择景区名称"
              clearable
              style="width: 100%"
              @change="onChangeSpot"
            >
              <el-option
                v-for="(item, index) in scenicOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 景区名称 end -->

          <!-- 公司名称 start -->
          <el-form-item label="公司名称" prop="companyId">
            <!-- <el-input type="text" v-model="validateForm.spotName" autocomplete="off" placeholder="请输入景区名称"></el-input> -->
            <el-select
              v-model="validateForm.companyId"
              filterable
              placeholder="请选择公司名称"
              clearable
              style="width: 100%"
              @change="onChangeCompany"
            >
              <el-option
                v-for="(item, index) in companyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 公司名称 end -->

          <!-- 联系人姓名 start -->
          <el-form-item label="联系人姓名" prop="name">
            <!-- <el-input type="text" v-model="validateForm.name" autocomplete="off" placeholder="请输入联系人姓名"></el-input> -->
            <el-select
              v-model="validateForm.name"
              filterable
              placeholder="请选择联系人姓名"
              clearable
              style="width: 100%"
			  @change="onChangeName"
            >
              <el-option
                v-for="(item, index) in nameOptions"
                :key="index"
                :label="item.userName"
                :value="item.userId"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 联系人姓名 end -->

          <!-- 联系人电话 start -->
          <el-form-item label="联系人电话" prop="phone">
            <el-input
              type="text"
              v-model="validateForm.phone"
              autocomplete="off"
              placeholder="请输入联系人电话"
            ></el-input>
          </el-form-item>
          <!-- 联系人电话 end -->

          <!-- 联系人地址 start -->
          <el-form-item label="联系人地址" prop="address">
            <el-input
              type="text"
              v-model="validateForm.address"
              autocomplete="off"
              placeholder="请输入联系人地址"
            ></el-input>
            <!-- <el-card class="address-card">
              <span class="card-text" v-if="!validateForm.spotId">
                根据景区名称获取
              </span>
              <span v-else>{{ validateForm.address }}</span>
            </el-card> -->
          </el-form-item>
          <!-- 联系人地址 end -->

          <!-- 默认寄存地址 start -->
          <el-form-item label="默认寄存地址" prop="mailAddress">
            <!-- <el-input type="text" v-model="validateForm.mailAddress" autocomplete="off" placeholder="请输入默认寄存地址"></el-input> -->
            <el-select
              v-model="validateForm.mailAddress"
              filterable
              placeholder="是否为默认寄存地址"
              clearable
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in mailAddressOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 默认寄存地址 end -->

          <!-- 大区经理 start -->
          <el-form-item label="大区经理" prop="managerName">
            <el-input
              type="text"
              v-model="validateForm.managerName"
              autocomplete="off"
              placeholder="请输入大区经理"
            ></el-input>
          </el-form-item>
          <!-- 大区经理 end -->

          <!-- 大区经理电话 start -->
          <el-form-item label="大区经理电话" prop="managerPhone">
            <el-input
              type="text"
              v-model="validateForm.managerPhone"
              autocomplete="off"
              placeholder="请输入大区经理电话"
            ></el-input>
          </el-form-item>
          <!-- 大区经理电话 end -->

          <!-- 级别 start -->
          <el-form-item label="级别" prop="level">
            <el-select
              v-model="validateForm.level"
              filterable
              placeholder="请选择级别"
              clearable
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in levelOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 级别 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'Address',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证手机号)
     * @params message - 提示信息
     */
    function checkPhoneMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          callback()
        } else {
          var phone = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/
          if (phone.test(value)) {
            callback()
          } else {
            return callback(new Error(name + '填写有误'))
          }
        }
      }
    }

    /**
     * 公共验证函数(验证必填手机号)
     * @params message - 提示信息
     */
    function checkRequirePhoneMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          var phone = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/
          if (phone.test(value)) {
            callback()
          } else {
            return callback(new Error(name + '填写有误'))
          }
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        地址类型: 'type',
        公司名称: 'companyName',
        景区名称: 'spotName',
        登录名: 'userName',
        角色: 'roleName',
        地址: 'address',
        联系人姓名: 'name',
        联系人电话: 'phone',
        默认寄存地址: 'mailAddress',
        大区经理: 'managerName',
        大区经理电话: 'managerPhone',
        级别: 'level'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      searchScenicOptions: [], // 搜索时景区选项
      scenicOptions: [], // 提交时景区选项
      companyOptions: [], // 提交时公司选项
      searchNameOptions: [], // 搜索时联系人列表
      nameOptions: [], // 提交时联系人列表

      radio: '1',
      searchTypeOptions: [
        {
          value: 1,
          label: '景区地址'
        },
        {
          value: 2,
          label: '工厂地址'
        },
        {
          value: 3,
          label: '库房地址'
        },
        {
          value: 4,
          label: '配件供应商地址'
        },
        {
          value: 5,
          label: '研发地址'
        }
      ], // 搜索时地址类型选项
      typeOptions: [
        {
          value: 1,
          label: '景区地址'
        },
        {
          value: 2,
          label: '工厂地址'
        },
        {
          value: 3,
          label: '库房地址'
        },
        {
          value: 4,
          label: '配件供应商地址'
        },
        {
          value: 5,
          label: '研发地址'
        }
      ], // 提交时地址类型选项

      mailAddressOptions: [
        {
          value: '1',
          label: '是'
        },
        {
          value: '2',
          label: '否'
        }
      ], // 默认寄存地址类型选项

      levelOptions: [
        {
          value: 1,
          label: '一级'
        },
        {
          value: 2,
          label: '二级'
        },
        {
          value: 3,
          label: '三级'
        }
      ], // 级别类型选项

      searchValidateForm: {
        spotId: '',
        name: '',
        phone: '',
        type: ''
      }, // 搜索表单验证对象
      validateForm: {},
      total: 0,

      dialogTitle: '',

      limitUpload: 3,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表

      updateUpload: true, // 是否显示upload组件

      // 搜索表单验证规则
      searchValidateFormRules: {
        // 联系人电话
        phone: [
          {
            validator: checkPhoneMethod('联系人电话'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单验证规则
      validateFormRules: {
        // 地址类型
        type: [
          {
            validator: checkRequireMethod('地址类型'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 景区名称
        spotId: [
          {
            validator: checkRequireMethod('景区名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 公司名称
        companyId: [
          {
            validator: checkRequireMethod('公司名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 地址
        address: [
          {
            validator: checkRequireMethod('地址'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 联系人电话
        phone: [
          {
            validator: checkRequirePhoneMethod('联系人电话'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 大区经理电话
        managerPhone: [
          {
            validator: checkPhoneMethod('联系人电话'),
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.initTableData()
    this.getAllReceiptScenic()
    this.getAllScenic()
    this.getAllUser()
    this.getAllCompany()
    this.getValidateForm()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        spotId: '' // 景区id
      }
      // console.log(this.validateForm);
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      this.loadingTable = true
      const { data: res } = await this.$http.get('/system/address/list', {
        params: {
          ...this.searchValidateForm,
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        // this.$message({
        //   message: res.msg,
        //   type: 'success',
        //   offset: 100,
        //   center: true,
        //   duration: 1000,
        // })
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    // /**
    //  * 获取所有景区列表(用的是`签约信息`中的`新增修改时景区列表`接口)
    //  */
    // async getAllScenic() {
    //   const { data: res } = await this.$http.get("/system/subscriptionInformation/allSpot")

    //   this.searchScenicOptions = res.data
    //   this.scenicOptions = res.data
    // },

    /**
     * 获取所有收货景区列表
     */
    async getAllReceiptScenic() {
      const { data: res } = await this.$http.get(
        '/system/factory_send/spot_list_all'
      )

      this.searchScenicOptions = res.map((item) => ({
        value: item.SPOT_ID,
        label: item.SCENIC_SPOT_NAME
      }))
    },

    /**
     * 获取所有景区列表
     */
    async getAllScenic() {
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList'
      )

      this.scenicOptions = res.data.map((item) => ({
        value: item.SCENIC_SPOT_ID,
        label: item.SCENIC_SPOT_NAME
      }))
    },

    /**
     * 获取所有用户列表(用的是“发货管理”中的员工列表)
     */
    async getAllUser() {
      const { data: res } = await this.$http.get(
        '/system/address/getAppUserBySpot',
        {
          params: {
            spotId: this.validateForm.spotId || null
          }
        }
      )

      if (res.state == '200') {
		  console.log(res)
        this.searchNameOptions = res.data
        this.nameOptions = res.data
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 获取所有公司列表(用的是`签约信息`中的`新增修改时公司列表`接口)
     */
    async getAllCompany() {
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/allCompany'
      )

      this.companyOptions = res.data
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
		console.log(this.validateForm)
      const { data: res } = await this.$http.post(
        '/system/address/add',
        this.validateForm
      )
	  console.log(this.validateForm)
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/address/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增地址管理信息'
    },

    // 重置
    onReset() {
      // 重新获取表单
      this.getSearchValidateForm()
      this.getValidateForm()
      this.currentPage = 1

      // this.selectDateState = '1'
      // this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.userName = parseFloat(rowObj.userName)

      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑地址管理信息'
    },

    // 点击删除时触发
    deleteRow(index, row) {
      // console.log(index, row);
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          // let data = this.$qs.stringify({
          //   id: row.id
          // })
          const { data: res } = await this.$http.delete(
            '/system/address/delete',
            {
              params: {
                id: row.id
              }
            }
          )
          // console.log(res);
          if (res.state == '200') {
            this.$message({
              type: 'success',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 1000
            })
            this.initTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 3000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 提交表单中景区选项变化时触发
    onChangeSpot(soptId) {
      // console.log(soptId)
      let obj = {}
      obj = this.scenicOptions.find((item) => {
        //这里的scenicOptions就是上面遍历的数据源
        return item.value === soptId //筛选出匹配数据，这里的soptId是你遍历数组每一项的label
      })
      var spotName = obj.label //  这里的spotName就是对应value的
      this.validateForm.spotName = spotName // 赋值给需要传给后台的表单验证对象

      this.getAllUser()
    },

    // 提交表单中公司选项变化时触发
    onChangeCompany(companyId) {
      // console.log(companyId);
      let obj = {}
      obj = this.companyOptions.find((item) => {
        //这里的scenicOptions就是上面遍历的数据源
        return item.COMPANY_ID === companyId //筛选出匹配数据，这里的soptId是你遍历数组每一项的label
      })
      var companyName = obj.COMPANY_NAME //  这里的SCENIC_SPOT_NAME就是对应value的
      this.validateForm.companyName = companyName // 赋值给需要传给后台的表单验证对象
    },
	onChangeName(userId){
		let obj = {}
		obj = this.nameOptions.find((item) => {
		  //这里的scenicOptions就是上面遍历的数据源
		  return item.userId === userId //筛选出匹配数据，这里的soptId是你遍历数组每一项的label
		})
		console.log(obj.userName)
		var userId = obj.userId //  这里的SCENIC_SPOT_NAME就是对应value的
		
		this.validateForm.userId = userId // 赋值给需要传给后台的表单验证对象
		this.validateForm.userName = obj.userName
		this.validateForm.name = obj.userName
	},

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },

    // 文件上传成功时的钩子
    handleSuccess(response, file, fileList) {
      // console.log(file);
      this.fileListUpload[0] = file
      this.fileTemp = file.raw

      this.reloadUpload()

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.$message({
            type: 'success',
            message: '导入成功！',
            offset: 100,
            center: true,
            duration: 1000
          })

          this.initTableData()
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
      this.fileListUpload = []
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '景区地址'
          } else if (row[value] == 2) {
            result = '工厂地址'
          } else if (row[value] == 3) {
            result = '库房地址'
          } else if (row[value] == 4) {
            result = '配件供应商地址'
          } else if (row[value] == 5) {
            result = '研发地址'
          }
        } else if (value == 'level') {
          if (row[value] == 1) {
            result = '一级'
          } else if (row[value] == 2) {
            result = '二级'
          } else if (row[value] == 3) {
            result = '三级'
          }
        } else if (value == 'mailAddress') {
          if (row[value] == '1') {
            result = '是'
          }
          if (row[value] == '2') {
            result = '否'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

